.header {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 20px;
    background-color: #f8f8f8;
    border-bottom: 1px solid #ddd;
    position: relative;
}

.header-left {
    display: flex;
    align-items: center;
    position: absolute;
    left: 20px;
}

.header-right {
    display: flex;
    align-items: center;
    position: absolute;
    right: 20px;
}

.header-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.logo {
    font-size: 24px;
    font-weight: bold;
}

.link {
    margin-right: 15px;
    text-decoration: none;
    color: #333;
}

.menu-icon {
    cursor: pointer;
    font-size: 24px;
    margin-right: 15px;
}

.slide-menu {
    position: fixed;
    top: 0;
    left: -250px;
    width: 250px;
    height: 100%;
    background-color: #333;
    color: white;
    transition: left 0.3s ease;
    z-index: 1000;
}

.slide-menu.open {
    left: 0;
}

.slide-menu-content {
    padding: 20px;
}

.slide-link {
    display: block;
    color: white;
    text-decoration: none;
    padding: 10px 0;
}

.slide-link:hover {
    text-decoration: underline;
}
